.slider__container {
  position: absolute;
  width: 687px;
  height: 680px;
  overflow: hidden;
  bottom: 0;
  right: 0;
}

.sldr {
  position: relative;
  width: 10000px;
  height: 680px;
}

.sldr img {
  float: left;
}

.right__arrow {
  position: absolute;
  bottom: 66px;
  left: 88px;
  display: block;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  cursor: pointer;
  background-color: #fff;
}

.left__arrow {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  cursor: pointer;
  background-color: #fff;
  position: absolute;
  bottom: 66px;
  left: 48px;
}

.left__arrow:hover,
.right__arrow:hover {
  opacity: 0.6;
}

.photo__slider {
  border-radius: 200px 0 0;
}

.arrow_img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.button__container {
  display: flex;
}

.slider {
  &__text {
    position: absolute;
    bottom: 48px;
    left: 144px;
  }
  &__intro {
    font-weight: bold;
    font-size: 13px;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: #fff;
  }
  &__quote {
    font-family: Open Sans, sans-serif;
    font-size: 14px;

    color: #fff;
  }
}
